.ed-panel {
  position: relative;
  /* display: flex; */
  flex-direction: column;
  justify-content: baseline;
  width: 100%;
  align-items: left;
}

#ed-row {
  display: flex;
  flex-direction: row;
  align-items: center ;
  width: 100%;
  padding: 0;
  margin: 0;
  /* margin-bottom: 16px; */
  justify-content: space-between;
}

.ed-panel img {
  width: 106px;
  height: 106px;
  margin: 0;
  padding: 0;
}

.ed-panel h3 {
  font-size: 48px;
  color: white;
  padding: 0;
  margin: 0;
}

.ed-panel h4 {
  font-size: 24px;
  color: white;
  padding: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}

.ed-panel h5 {
  font-size: 18px;
  color: white;
  padding: 0;
  font-style: italic;
  margin: 0;
}

.ed-panel p {
  font-size: 16px;
  margin: 0;
  margin-bottom: 8px;
  color: white;
}

#ed-details {
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
