/* Ed.css */

.project-panel-active {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  align-items: left;
  border-radius: 16px;
  padding: 32px;
  opacity: 1;
  transition: 0.5s;
}


.project-row {
  display: flex;
  flex-direction: row;
  justify-content: left;
  gap: 16px;
  align-items: center;
}

.project-row-sb {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 32px;
  margin: 0;
  padding: 0;
}

#see-more-github {
  font-size: 16px;
  transition: 1s;
  padding: 0;
  margin: 0;
}

.project-row #bf {
  font-weight: 600;
}

.project-row #nbf {
  font-weight: 100;
  font-style: italic;
}

.project-flex {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  text-align: left;
  width: 100%;
  min-height: 180px;
  align-items: left;
}

.project-panel-active img {
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  border-radius: 4px;
  transition: 1s;
}

.project-panel-active img:hover {
  background-color: var(--purple);
}

.project-panel-active h2 {
  font-size: 36px;
  color: white;
  padding: 0;
  margin: 0;
}

.project-panel-active h3 {
  font-size: 36px;
  color: white;
  padding: 0;
  margin: 0;
}

.project-panel-active h4 {
  font-size: 18px;
  color: white;
  padding: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}

.project-panel-active h5 {
  font-size: 14px;
  color: white;
  padding: 0;
  font-style: italic;
  margin: 0;
}

.project-panel-active p {
  font-size: 14px;
  margin: 0;
  padding-top: 24px;
  margin-bottom: 8px;
  color: white;
}