/* Ed.css */

.work-panel {
  position: relative;
  /* display: flex; */
  flex-direction: column;
  justify-content: baseline;
  width: 100%;
  align-items: left;
}

#work-row {
  display: flex;
  flex-direction: row;
  align-items: center ;
  width: 100%;
  height: 80px;
  padding: 0;
  margin: 0;
  justify-content: space-between;
}

.work-panel img {
  width: 80px;
  height: 80px;
  margin: 0;
  padding: 0;
}

.work-panel h3 {
  font-size: 36px;
  color: white;
  padding: 0;
  margin: 0;
}

.work-panel h4 {
  font-size: 18px;
  color: white;
  padding: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}

.work-panel h5 {
  font-size: 14px;
  color: white;
  padding: 0;
  font-style: italic;
  margin: 0;
}

.work-panel p {
  font-size: 14px;
  margin: 0;
  margin-bottom: 8px;
  color: white;
}

#work-details {
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pivot {
  margin: 0;
  width: 60%;
  height: 90%;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.pivotout {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  opacity:0;
  width:60%;
  height:100%;
  z-index: -1;
  transition: opacity 1s;
}
.pivotin {
  display: flex;
  flex-direction: row;
  align-items: center;
  opacity:1;
  width:100%;
  height:100%;
  transition: opacity 1s
}

.work-panel li {
  font-size: 14px;
  list-style-type: "    x    ";
  color: white;
  padding: 4px;
  transition: 1s;
}

.work-panel li:hover {
  list-style-type: "    x    ";
  transform: translate(8px,0);
}

.work-panel li::marker {
  color: var(--blue-half);
  transition: 1s;
}

.work-panel li:hover::marker {
  color: var(--purple);
}