/* App.css */

:root {
  --blue-half: rgba(11, 0, 218, 0.4);
  --purple-half: rgba(164, 55, 242, 0.4);
  --blue: rgb(11, 0, 218); 
  --purple: rgb(164, 55, 242);
  --black: rgb(0, 0, 0);
}
.App {
    position: relative;
    font-family: 'Trebuchet MS', sans-serif;
    margin: 0 auto;
    width: 100%;
    background-color: var(--black);
    padding: 0;
  }
  
  nav {
    position: fixed;
    z-index: 1;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 48px;
    background-color: rgb(20,20,20);
    padding: 0;
    display: flex;
    flex-direction: row; 
    font-size: 24px;
    align-items: center;
  }
  
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 48px;
  }
  
  nav ul li {
    height: 100%;
    padding: 0 16px;
    cursor: pointer;
    transition: 0.5s;
  }

  nav ul li:hover {
    background: rgb(35, 35, 35);
    transition: 0.5s;
  }
  
  nav ul li a {
    height: 100%;
    color: #fff;
    transition: 0.5s;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  nav ul li a.active {
    color: blue;
    transition: 0.5s;
  }

  .content {
    width: 100%;
  }
  
  .section {
    min-height: calc(100vh);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 1s ease;
  }

  .flex-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 80%;
    background: linear-gradient(90deg, var(--blue-half) 0%, var(--purple-half) 100%);
    height: max(640px, 80%);
    gap: 48px;
    padding: 32px;
    border-radius: 32px;
  }

  .flex-container-col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    background: linear-gradient(90deg, var(--blue-half) 0%, var(--purple-half) 100%);
    height: max(640px, 80%);
    gap: 24px;
    padding: 32px;
    border-radius: 32px;
  }

  .flex-container-col-nobg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: max(640px, 80%);
    gap: 24px;
    padding: 32px;
    border-radius: 32px;
  }

  .flex-container-nobg {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: max(640px, 80%);
    gap: 48px;
    padding: 32px;
    border-radius: 32px;
  }

  .ed-container {
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    width: 100%;
    align-items: left;
    opacity: 0.9;
    background: linear-gradient(90deg, var(--blue-half) 0%, var(--purple-half) 100%);
    border-radius: 32px;
    padding: 24px;
  }

  .ed-container:hover {
    opacity: 1;
    transition: 0.5s;
  }

  .ed-container h1 {
    font-size: 42px;
    color: white;
  }
  .ed-container h2 {
    font-size: 24px;
    color: white;
  }
  
  h1 {
    font-size: 96px;
    margin-bottom: 20px;
    color: white;
  }
  h2 {
    font-size: 72px;
    margin-bottom: 20px;
    color: white;
  }
  h3 {
    font-size: 48px;
    margin: 0;
    color: white;
  }

  p {
    font-size: 22px;
    margin-bottom: 20px;
    color: white;
  }

  #abouttext {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .content-placeholder {
    font-size: 24px;
    color: #999;
  }

  .next {
    z-index: 3;
    position: fixed;
    bottom: 16px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: gray;
    opacity: 1;
  }

  .next a {
    font-size: 32px;
    border-radius: 20%;
    padding: 4px;
    position: relative;
    cursor: pointer;
    transition: transform ease 0.5s, 0.5s;
  }

  .next a:hover {
    color: white;
    transform: translate(0, 16px);
  }

  .foot {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    height: 64px;
    padding-left: 16px;
    background-color: var(--black);
    z-index: 4;
  }

  .foot p {
    color: white;
    font-size: 24px;
    margin: 0;
    padding: 0;
  }

  .foot #foot-arrow {
    z-index: 3;
    position: relative;
    animation: pulse-right 1s infinite linear;
  }

  @keyframes pulse-right {
    0% {
      transform: translate(16px, 0);
    }
    30% {
      transform: translate(0px, 0);
    }
    70% {
      transform: translate(0px, 0);
    }
    100% {
      transform: translate(16px, 0);
    }
  }

  .block h3 {
    margin-bottom: 16px;
  }


  .block {
    width: 40%;
    align-self: stretch;
    background: linear-gradient(0deg, var(--blue-half) 0%, var(--purple-half) 100%);
    padding: 32px;
    border-radius: 32px;
  }

  
  .block li {
    position: relative;
    padding: 4px;
    color: white;
    transition: transform ease 1s, 1s;
  }

  .block li:hover {
    transform: translate(8px, 0);
    font-weight: 100;
    transition: 1s;
  }

  #courses-taken {
    list-style-type: "    x    ";
    /* padding: 0; */
  }

  #courses-taken li::marker {
    color: var(--blue-half);
    transition: 1s;
  }

  #courses-taken li:hover::marker {
    color: var(--purple);
  }

  .skill {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: white;
    transition: 1s;
  }

  #tech-list li {
    list-style-type: "    x    ";
    padding: 4px;
    color: var(--blue-half);
    transition: 1s;
  }

  #tech-list li:hover {
    list-style-type: "    x    ";
    /* padding: 0; */
    color: var(--purple);
    transform: translate(8px,0);
  }

.typewriter h1 {
  color: white;
  overflow: hidden;
  border-right: .1em solid var(--blue);
  white-space: nowrap;
  margin: 0 auto;
  animation: 
    typing 3s steps(30, end),
    blink-caret 0.5s step-end 7;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: var(--blue)}
}

.contact {
  position: fixed;
  bottom: 0;
  right: 0;
  height: 64px;
  display: flex;
  flex-direction: row;
  z-index: 6;
}

.contact a {
  height: 64px;
  width: 64px;
  transition: 0.5s ease;
}

.contact img {
  height: 40px;
  width: 40px;
  padding: 12px;
  transition: 0.5s ease;
}

.contact a {
  display: flex;
  align-items: center;
  border-radius: 16px;
  justify-content: center;
}

.contact a:hover {
  transform: translate(0, -8px);
  background-color: var(--purple-half);
}

.section a {
  color: white;
  transition: 1s;
}

.section a:hover {
  color: var(--purple);
}